@import '../Variables.scss';

header
{
    background-color: $primary-main-color;
    font-family: $primary-font-family;
}

.logotype
{
    display: flex;
    text-decoration: none;
    color: $secondary-sub-color;
    font-size: 0;

    p
    {
        font-size: $medium-size;
        margin: 0;
        
        &::first-letter
        {
            color: $secondary-main-color;
            font-size: $large-size;
        }
    }

    &:hover
    {
        color: $secondary-sub-color;
        text-decoration: none;
    }
}

.navButton
{
    display: inline-block;
    color: $secondary-sub-color;
    font-size: calc(#{$small-size} * 1.25);

    @media screen and (min-width: $screen-lg-min-width)
    {
        display: none;
    }
}

.navList
{
    display: flex;
    flex-direction: column;
    justify-items: flex-end;
    align-items: flex-end;
    list-style: none;
    margin: 0 0 0 auto;
    padding: 0;

    li
    {
        display: block;
        position: relative;

        a
        {
            display: inline-block;
            position: relative;
            text-decoration: none;
            color: $secondary-sub-color;
            font-size: calc(#{$small-size} * 1.25);
            padding: calc(#{$small-size} * 1.375);
        }

        &::after
        {
            display: block;
            position: absolute;
            background: $secondary-sub-color none repeat scroll 0 0;
            height: 0.25rem;
            left: 50%;
            bottom: 0;
            width: 0;
            transition: width 250ms ease-in-out, left 250ms ease-in-out;
            content: '';
        }

        &:hover:after
        {
            width: 100%;
            left: 0;
        }
    }

    @media screen and (min-width: $screen-lg-min-width)
    {
        flex-direction: row;
        justify-items: center;
        align-items: center;
    }
}